<template>
    <div class="app-shopcart">
        <!-- <h1>shopCart</h1> -->
        <!-- 1.添加card.html卡片 -->
        <header class="hearder-top mui-bar mui-bar-nav">
            <a @click="btnLogin" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title">shopCart</h1>
        </header>
        <div class="mui-card">
			<div class="hearder-top mui-card-header">
                <h4>商品列表</h4>
                <!-- 1.添加复选框 -->
                <input type="checkbox" @click="selectAll" :checked="allcb">
                <!-- 2.为复选框绑定点击事件 -->
                <!-- 3.如果选中状态，所有商品选中 -->
                <!-- 4.如果清除状态，所有商品清除 -->
            </div>
            <br><br>
            <hr>  
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
					<!-- 商品信息的开始 -->
                    <ul class="mui-table-view">
                        <li class="mui-table-view-cell mui-media" v-for="(item,i) of list" :key="i">
                            <a href="javascript:;">
                                <div class="mui-media-body">
                                    <div class='mui-ellipsis'>
                                        <!-- 复选框 -->
                                        <div>
                                            <img :src="item.pimg" class="itempimg" alt="">
                                        </div>
                                        <input type="checkbox" :data-id="item.id" :data-i="i" :checked="item.cb" @click="modifyItem">
                                        <!-- 商品详情 -->
                                        <div><h2>{{item.pname}}</h2></div>
                                        <div><h4>￥{{item.price}}</h4></div>       

                                        <!--加减 限定最小值和最大值(1~99) -->
                                        <div class="mui-numbox" data-numbox-min='1' data-numbox-max='99'>
                                            <button class="mui-btn mui-btn-numbox-minus" type="button" @click="addsubtraction">-</button>
                                            <input id="test" class="mui-input-numbox" type="number" :value="item.count" v-on:input ="inputFunc"/>
                                            <button class="mui-btn mui-btn-numbox-plus" type="button" @click="addaddition">+</button>
                                        </div>
                                        <br>
                                        <hr>
                                        <br>
                                        <!-- 删除按钮 -->
                                        <div>
                                            <input type="button" value="删除" :data-id="item.id" :data-idx="i" @click="removeItem">
                                        </div>                                        
                                    </div>
                                </div>  
                            </a>
                        </li>
                    </ul>
					<!-- 商品信息的结束 -->
				</div>
			</div>
            <hr>
            <br><br><br>
			<div class="footer-bottom mui-card-footer">
                <!-- 删除 -->
                <div>
                    <button type="danger" @click="delItem">删除选中的商品</button>
                </div>
                <div>
                    <div>
                        <!-- Buy the selected goods -->
                        <div ><h4>￥{{purchase}}</h4></div>
                        <button type="dark" @click="selected">购买选中的商品</button>
                    </div>
                </div>
                
                <!-- 价格 -->
            </div>
            
		</div>
        <!-- 2.添加媒体条目 -->
      
        <!-- 3.发送请求获取购物车列表 -->
        <!-- 4.保存显示 -->
    </div>    
</template>
<script>
//1.当前的购物车商品内容前添加复选框
//2.为复选框添加自定义属性id
//3.cart-footer 添加按钮 删除指定商品
//4.为按钮绑定事件获取选中商品id

import {Toast} from "mint-ui";
export default {
    inject:['reload'],//注入reload方法
    data(){
        return{
           list:[],//购物车状态
           pname:"",
           price:"",
           count:"",
           purchase:"",
           allcb:false//全选状态  默认被选中
        }
    },
    created(e){
         this.loadMore();//购物车内容
         //写这里是因为要先引入，而加减明显不需要，所以隐去
        //  this.addsubtraction();//减法
        //  this.addaddition();//加法
         this.Purchasegoods();//商品总价
    },
    methods:{
        //返回上一个操作页面
        btnLogin(){
            this.$router.go(-1);
        },
        //购买键
        selected(e){
            // console.log(e);
            // console.log(e.path[3].children[1].innerText.split('￥')[1]);
            var mar = e.path[3].children[1].innerText.split('￥')[1];
            // Toast('购买成功支付￥'+mar);
            Toast('233333');
        },
        //防止数值修改
        inputFunc(){
           var ser = setTimeout(() => {
                this.reload();//刷新        
            }, 0);
        },
        //减法
        addsubtraction(e){ 
            // console.log(pname);
            // console.log(price);
            // console.log(e);
             var pr = e.path[2].children[3].innerText.split('￥');
             this.price = pr[1];//商品价格
             this.pname = e.path[2].children[2].innerText;//商品名称
            // console.log(e);
             this.count = e.path[2].children[4].children[1].value;//数量
            //  console.log(this.count);

            var pname = this.pname
            var price = this.price
            var count = this.count
            // console.log(val);
            // console.log(pr);

            // console.log(pname);
            // console.log(price);
            // console.log(count);
            var arr = 0;
            // val--;
            // console.log(e);
            // console.log(e.path[0]);
            // console.log(e.path[1].children[1].value);
            // console.log(val);
            if(count>=1){
                arr = count-1;
                e.path[1].children[1].value = arr;
            }else if(count<1){
                count=1;
                arr = count;
                e.path[1].children[1].value = arr;
                return;
            } 
            var url="http://127.0.0.1:3000";
            url+="/addsub?pname="+pname;
            url+="&price="+price;
            url+="&count="+count;
            this.axios.get(url).then(result=>{
            //    console.log(result);
                // this.reload();//刷新
                   this.loadMore();
                //  var rows = result.data.data;
                //  console.log(rows);
             })
        },
        //加法
        addaddition(e){ 
            // console.log(pname);
            // console.log(price);
            // console.log(e);
             var pr = e.path[2].children[3].innerText.split('￥');
             this.price = pr[1];//商品价格
             this.pname = e.path[2].children[2].innerText;//商品名称
            // console.log(e);
             this.count = e.path[2].children[4].children[1].value;//数量
            //  console.log(this.count);

            var pname = this.pname
            var price = this.price
            var count = this.count
            // console.log(val);
            // console.log(pr);

            // console.log(pname);
            // console.log(price);
            // console.log(count);
            var arr = 0;
            // val--;
            // console.log(e);
            // console.log(e.path[0]);
            // console.log(e.path[1].children[1].value);
            // console.log(val);
           if(count>=1){
                arr = count-1+2;
                e.path[1].children[1].value = arr;
                // console.log(1111);
            }
            var url="http://127.0.0.1:3000";
            url+="/addaddition?pname="+pname;
            url+="&price="+price;
            url+="&count="+count; 
            this.axios.get(url).then(result=>{
                // console.log(result);
                // console.log(111111);
                // this.reload();//刷新  
                this.loadMore();
                  
                   
                //  var rows = result.data.data;
                //  console.log(rows);
             })
        },
        //全选
        selectAll(e){
            // console.log(1325);
            // 1.获取全选按钮的状态
            var rs = e.target.checked;
            // 2.遍历数组中的所有元素
            for(var item of this.list){
                item.cb=rs;
            }
            // 3.item.cb = 全选状态
            // 4.修改全选数据状态
            this.allcb = rs;
            this.Purchasegoods();
            // this.Displa();
        },
        //修改复选框的状态 选中 取消
        modifyItem(e){
            // console.log(66666666666);
            // console.log(e);
            // 1.获取当前元素选中状态 true false
            var rs = e.path[0].checked;
            // console.log(rs);
            // 2.当前元素在数组中下标
            var i = e.target.dataset.i;
            // console.log(i);
            // 3.将状态赋值当前数组下标中对象的cb值
            this.list[i].cb = rs;
            // console.log(this.list);
            // 判断全选复选框状态 true false
            // 1.获取数组个数
            var size1=this.list.length;
            // 2.计算数组中cb true 元素个数
            var count = 0;
            // 3.数组个数 == cb true 个数[累加和]
            for(var item of this.list){
                if(item.cb){
                    count++;
                }
            }
            // console.log(size1+":"+count);
            // 4.将allcb = true
            if(size1==count){
                this.allcb = true;         
            }else{
                this.allcb = false;
            }
            this.Purchasegoods();
             
            // this.Displa();
            // 5.否则allcb = false
        },
        //显示价额Display price
        Displa(){
            // console.log(2333);
            // [{cb:true,id:1},{cb:true,id:3}]
            //"1,3"
            // 1.创建空字符串
            var html="";
            var pri ="";
            var con ="";
            // 2.遍历数据中元素
            for(var item of this.list){
                // 3.判断cb==true
                if(item.cb){
                    // 4.保存id拼凑字符串"1,6,7"
                    html += item.id+",";
                    pri += item.price +",";
                    con +=item.count +",";
                    // console.log(html);
                    // console.log(pri);
                    // console.log(con);
                } 
            }
            // console.log(html);
            // 5.截取字符串去除最后
            //substring(?,?)
            //?截取的起始下标
            //截取几个字符
            html = html.substring(0,html.length-1).split(',');
            pri  = pri.substring(0,pri.length-1).split(',');
            con  = con.substring(0,con.length-1).split(',');
            // console.log(html);
            // console.log(pri);
            // console.log(con);
            if(html.length==0){
                Toast("请选中您购买需要的商品");
                return;
            }
            var price=0
            for(var i=0;i<html.length;i++){
                price +=pri[i]*con[i];
            }
            // console.log(price);
            // 6.发送ajax ids=1,6,7
            var url = "http://127.0.0.1:3000";
            url+="/commodity?price="+price;
            this.axios.get(url).then(result=>{
                // console.log(result);
               if(result.data.code==1){
                   if(result.data.data[0]!=undefined){
                       this.purchase = result.data.data[0].price;
                    //    console.log(this.purchase);
                   }
                //    this.loadMore();
               }
            });
            // 7.获取返回数据提示，删除成功或失败
        },
        //选中多个商品计算
        Purchasegoods(){
            // console.log(2333);
            // [{cb:true,id:1},{cb:true,id:3}]
            //"1,3"
            // 1.创建空字符串
            var html="";
            var pri ="";
            var con ="";
            // 2.遍历数据中元素
            for(var item of this.list){
                // 3.判断cb==true
                if(item.cb){
                    // 4.保存id拼凑字符串"1,6,7"
                    html += item.id+",";
                    pri += item.price +",";
                    con +=item.count +",";
                    // console.log(html);
                    // console.log(pri);
                    // console.log(con);
                } 
            }
            // console.log(html);
            // 5.截取字符串去除最后
            //substring(?,?)
            //?截取的起始下标
            //截取几个字符
            html = html.substring(0,html.length-1).split(',');
            pri  = pri.substring(0,pri.length-1).split(',');
            con  = con.substring(0,con.length-1).split(',');
            // console.log(html);
            // console.log(pri);
            // console.log(con);
            if(html.length==0){
                Toast("请选中您购买需要的商品");
                return;
            }
            var price=0
            for(var i=0;i<html.length;i++){
                price +=pri[i]*con[i];
            }
            // console.log(price);
            // 6.发送ajax ids=1,6,7
            var url = "http://127.0.0.1:3000";
            url+="/Purgoods?price="+price;
            this.axios.get(url).then(result=>{
                // console.log(result);
                this.Displa();         
            });
            // 7.获取返回数据提示，删除成功或失败
        },
        // 选中多个商品全部删除
        delItem(){
            // console.log(2333);
            // [{cb:true,id:1},{cb:true,id:3}]
            //"1,3"
            // 1.创建空字符串
            var html="";
            // 2.遍历数据中元素
            for(var item of this.list){
                // 3.判断cb==true
                if(item.cb){
                    // 4.保存id拼凑字符串"1,6,7"
                    html += item.id+",";
                } 
            }
            // console.log(html);
            // 5.截取字符串去除最后
            //substring(?,?)
            //?截取的起始下标
            //截取几个字符
            html = html.substring(0,html.length-1);
            // console.log(html);
            if(html.length==0){
                Toast("请选中您需要删除的商品");
                return;
            }
            // 6.发送ajax ids=1,6,7
            var url = "http://127.0.0.1:3000";
            url+="/removeMItem?ids="+html;
            this.axios.get(url).then(result=>{
                // console.log(result);
                this.loadMore();
            });
            // 7.获取返回数据提示，删除成功或失败
        },
        //选中单个商品删除
        removeItem(e){
            // 1.获取购物车的商品id
            var id = e.target.dataset.id;
            var idx = e.target.dataset.idx;
            // console.log(id);
            // console.log(idx);
            // 2.创建url
            var url="http://127.0.0.1:3000";
            url+="/removeItem";
            var postData = this.qs.stringify({
                id:id
            });
            // 3.发送ajax请求
            this.axios.post(url,postData).then(result=>{
                // console.log(result);
               if(result.data.code==1){
                   //1.获取下标
                   //2.依据下标删除指定数组商品
                    this.list.splice(idx,1);
                   Toast("删除成功");
               }else{
                    Toast("删除失败");
               }
            });
            // 4.获取返回结果
            // 5.提示删除结果
            // 6.list对应商品项删除
        },
        //加载当前用户的购物车列表
        loadMore(e){
            // 1.创建url地址
            var url="http://127.0.0.1:3000";
            url+="/getshopCart";
            // 2.发送ajax请求
            // 3.获取返回数据
            // 4.保存list
            this.axios.get(url).then(result=>{
                // console.log(result);
                // console.log(result.data.data);
                // //this.list = result.data.data;
                // // 在模板中循环数组显示数据...
                // var rows = this.list.concat(result.data.data);
                // this.list=rows;
                // // 5.循环显示
                // 6.获取返回数组
                // console.log(result);
                if(result.data.code==1){
                     var rows = result.data.data;
                    // 7.为数组中每个对象添加属性cb初始值false
                    for(var item of rows){
                        item.cb = false;
                    }
                    // 8.将数据保存在list中
                    this.list = rows;
                    // console.log(this.list);
                    // console.log(this.list);
                }else{
                    this.$router.push({ path: '/personal'});
                }
               
            });
            
        }
    }
}
//为复选框绑定(点击)事件
//选中某个商品 * 将对应的数组对象cb=true
//              清空对应的数组对象cb=false
</script>
<style>
.app-shopcart .hearder-top.mui-card-header{
    width: 100%;
    position: fixed;
    top: 6%;
    left: 0%;
    z-index: 999;
    background: #f7f7f7;
}
.app-shopcart .footer-bottom.mui-card-footer{
    width: 100%;
    left: 0%;
    bottom: 6%;
    position: fixed;
    background: #f7f7f7;
}
.itempimg{
    width: 100%;
}
</style>



