<template>
  <div class="app-search">
    <div class="mui-card">
       <header class="hearder-top mui-bar mui-bar-nav">
            <a @click="btnLogin" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title">search</h1>
        </header>
				<div class="mui-card-header">
          <input type="text" v-model='kword' placeholder="搜索菜名">
        </div>
        <div class="mui-card-content">
          <div class="mui-card-content-inner ">
            <div class='mui-card-ul'>
              <p>热门搜索</p>
              <ul class='mui-card-hot-search'>
                <li><router-link to='#'>茄子炖土豆</router-link></li>
                <li><router-link to='#'>卤香滑鸡 </router-link></li>
                <li><router-link to='#'>鸡块炖时蔬</router-link></li>
                <li><router-link to='#'>土豆蒸排骨</router-link></li>
                <li><router-link to='#'>榛蘑烧小白菜</router-link></li>
                <li><router-link to='#'>香蕉红薯糯米饼</router-link></li>
              </ul>
            </div>
            <div class='mui-card-list hide'>
              <p></p>
              <ul class='mui-card-hot-search mui-card-songs'>
                <li class='first-hot'>
                  <p>最佳匹配</p>
                  <img :src="listt.Csrc" alt="" >
                  <div class='songs-hot'> 
                    <h1><span>名称:<p>{{listt.Cname}}</p></span></h1>
                    <i>{{listt.Csubtitle}}</i>
                  </div>
                </li>
                <li v-for="(item,i) of list" :key="i">
                  <router-link :to="'/NewsInfo?nid='+item.Cid">
                    <div class='mui-card-list-content'>
                      <span>
                        <h1>{{item.Cname}}</h1>
                      </span>
                      <p>
                        {{item.Csubtitle}}
                        <span><h2>￥{{item.Cmoney}}</h2></span>
                      </p>
                      <span><h4>{{item.Cdetails}}</h4></span>
                    </div> 
                    <img :src="item.Csrc" alt="">
                  </router-link>
                </li>
              </ul>
            </div>
          </div>
        </div>
    </div>
    <router-view ref='childol'></router-view>
  </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
  data(){
    return{
      kword:'',//输入框输入内容
      list:[],//接收返回数据的容器
      listt:[]//截取数据的容器
    }
  },
  methods:{
    btnLogin(){
       this.$router.go(-1);
    },
    onhide(){
      var a=document.getElementsByClassName('mui-card-ul')[0]
      var b=document.getElementsByClassName('mui-card-list')[0]
      a.className='mui-card-ul hide'
      b.className='mui-card-list' 
    },
    onshow(){
      var a=document.getElementsByClassName('mui-card-ul')[0]
      var b=document.getElementsByClassName('mui-card-list')[0]
      a.className='mui-card-ul'
      b.className='mui-card-list hide'
    }
  },
  watch:{
    kword(){
      //至少输入一个字才能搜索
      if(this.kword.length>0){    
        var url='search?kwords='+this.kword
        this.axios.get(url).then(result=>{
            // console.log(111111);
            // console.log(result);
          if(result.data.code=='1'){
            // console.log(222222222);
            // console.log(result);
            this.list=result.data.data
            this.listt=this.list[0]
            var d=document.getElementsByClassName('first-hot')[0]
             if(this.listt.Csrc==undefined){
                d.className='first-hot hide'
              }else{
                d.className='first-hot show'
              }
          }else if(result.data.code!='1'){
            Toast('暂无此信息')
            var d=document.getElementsByClassName('mui-card-list')[0]
            d.className='mui-card-list hide'
          }
        })
        this.onhide()
      }else if(this.kword.length<=0){
        this.onshow()
      }
    }
  },
}
</script>
<style>
body{background-color: #fff !important;}
.mui-card{
  margin:0 !important;
  margin-top:20px !important;
}
.mui-card .mui-card-header input{
  margin:0 ;
  color:black;
  background-color: #ebecec;
  border-radius:50px;
  height:30px;
  padding-left:30px;
  font-size:14px;
}
/* 推荐歌单 */
.show{display:block !important}
.hide{display:none !important;}
.mui-card-hot-search{
  list-style:none;
  display:flex;
  flex-wrap:wrap;
  width:100%;
  padding:0
}
.mui-card-hot-search li{
  border:1px solid #ebecec;
  margin:10px 0 0 5px;
  padding:0 10px;
  border-radius:35px;
  line-height:32px;
  
}
/* 搜索歌单 */
.mui-card-songs li{
  width:100%;
  display:flex;
  justify-content:space-between 
}
.mui-card-hot-search li a{  
  color:black !important
}
.mui-card-list-content>p{
  font-size:12px;
  line-height:18px
}
.mui-card-songs li:not(:first-child) img{
    width: 80px;
    height: 80px;
    /* position: relative;
    top: -25px;
    left: 180px; */
}
.mui-card-list-content span{
  color:#507daf;
  font-size:17px;
}
.mui-card-songs li:first-child{
  position:relative;
  height: 60px;
  border-radius:0 !important;
  border-left:0;
  border-right:0
}
.mui-card-songs li:first-child p{
  position:absolute;
  top:-37px;
}
.mui-card-songs li:first-child img{
  position:absolute;
  top:5px;
  width:50px;
  height:50px;
}
.songs-hot span,.songs-hot i{
  position:absolute
}
.songs-hot span{
  left:70px;
  font-size:18px;
}
.songs-hot span p{
  color:#507daf;
  width: 130px;
  left:45px !important;
  top:0 !important;
  font-size:18px 
}
.songs-hot i{
  top:30px;
  left:70px;
  color:#8f8f94
}
</style>



