<template>
    <div class="app-Login">
        <header class="mui-bar mui-bar-nav">
            <a @click="appLogin" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
			<h1 class="mui-title">登录</h1>
		</header>
        <div class="mui-content">
			<form id='login-form' class="mui-input-group">
				<div class="mui-input-row">
					<label>账号</label>
					<input id='account' type="text" class="mui-input-clear mui-input" placeholder="请输入账号" name="uname" v-model="uname">
				</div>
				<div class="mui-input-row">
					<label>密码</label>
					<input id='password' type="password" class="mui-input-clear mui-input" placeholder="请输入密码" name="upwd" v-model="upwd">
				</div>
			</form>
			<div class="mui-content-padded">
				<button id='login' type="button" class="mui-btn mui-btn-block mui-btn-primary" @click="btnLogin">登录</button>
				<div class="link-area"><a id='reg' @click="btnregister">注册账号</a> <span class="spliter">|</span> <a id='forgetPassword'>忘记密码</a>
				</div>
			</div>
			<div class="mui-content-padded oauth-area">

			</div>
		</div>
    </div>
</template>
<script>
//1：引入mint-ui Toast 局部使用
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            uname:"",
            upwd:""
        }
    },
    methods:{
        appLogin(){
            this.$router.go(-1);
        },
        btnregister(){
            var ser = setTimeout(() => {
                this.$router.push({ path: '/register'});              
            }, 10);
        },
        btnLogin(){
            // console.log(11111111);
            // 1.用户输入用户名和密码
            var u = this.uname;
            var p = this.upwd;
            // console.log(u+":"+p);
            // 2.创建正则表达式验证用户名和密码格式是否正确
            var reg = /^[a-z0-9_]{3,8}$/i;
            if(!reg.test(u)){
                //提示信息有误
                Toast("用户名格式不正确");
                return;
            }
            if(!reg.test(p)){
                Toast("密码格式不正确");
                return;
            }
            // 3.发送ajax
            // console.log(3333333333);
            var url = "login?uname="+u+"&upwd="+p;
            this.axios.get(url).then(result=>{
                // console.log(4444444444);                
                // console.log(result.data.code);
                if(result.data.code==1){
                    Toast("登陆成功");
                    // console.log(result);
                    // console.log(this.uname);
                    
                    // sessionStorage.setItem("Uname",this.uname);
                    // var Uname = sessionStorage.getItem("Uname");
                    // console.log(Uname);
                    // this.$store.state.Uname=this.uname;
                    // console.log(this.$store.state.Uname);
                    this.$router.push({ path: '/personal'});
                    // var ser = setTimeout(() => {
                    //     this.$router.push({ path: '/personal'});              
                    // }, 1000);
                    return;
                }else{
                    Toast("用户名或密码有误");
                }
            });
            // 4.获取服务器返回结果
            // 5.提示用户
        }
    }
}
</script>
<style>
.mui-content-padded {
	margin-top: 25px;
}

.mui-btn {
	padding: 10px;
}

.link-area {
	display: block;
	margin-top: 25px;
	text-align: center;
}

.spliter {
	color: #bbb;
	padding: 0px 8px;
}
.mui-input-group .mui-input-row {
    margin-top: 10px;
}
.app-Login .mui-bar-nav ~ .mui-content {
    padding-top: 0;
}
</style>



