<template>
    <div class="app-register">
        <header class="mui-bar mui-bar-nav">
            <a @click="btnLogin" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title">注册</h1>
        </header>
        <div class="mui-content">
            
			<form id='login-form' class="mui-input-group">
				<div class="mui-input-row">
					<label>账号</label>
					<input id='account' type="text" class="mui-input-clear mui-input" placeholder="请输入账号" name="uname" v-model="uname" v-on:input ="inputFunc">
				</div>
				<div class="mui-input-row">
					<label>密码</label>
					<input id='password' type="password" class="mui-input-clear mui-input" placeholder="请输入密码" name="upwd" v-model="upwd">
				</div>
                <div class="mui-input-row">
					<label>验证</label>
					<input id='verification' type="password" class="mui-input-clear mui-input" placeholder="请确认密码" name="uver" v-model="uver">
				</div>
                <div class="mui-input-row">
					<label>邮箱</label>
					<input id='mailbox' type="mailbox" class="mui-input-clear mui-input" placeholder="请输入邮箱" name="uemail" v-model="uemail">
				</div>
                <div class="mui-input-row">
					<label>电话</label>
					<input id='phone' type="phone" class="mui-input-clear mui-input" placeholder="请输入手机号码" name="uphone" v-model="uphone">
				</div>
			</form>
			<div class="mui-content-padded">
				<button id='login' type="button" class="mui-btn mui-btn-block mui-btn-primary" @click="btnregister">注册</button>
			</div>
			<div class="mui-content-padded oauth-area"></div>
		</div>
    </div>
</template>
<script>
//1：引入mint-ui Toast 局部使用
import {Toast} from "mint-ui"
// import {router} from "vue-router"
export default {
    data(){
        return{
            uname:"",
            upwd:"",
            uver:"",
            uemail:"",
            uphone:""
        }
    },
    methods:{
        inputFunc(){
            var u = this.uname;
            var url = "inquire?uname="+u;
            this.axios.get(url).then(result=>{
                // console.log(4444444444);                
                // console.log(result);
                // console.log(result.data.code);
               if(result.data==0){
                    return Toast("用户名已占用");
                }
            });
        },
        btnLogin(){
            // var ser = setTimeout(() => {
            //     this.$router.push({ path: '/Login'});              
            // }, 10);
            this.$router.go(-1);
        },
        btnregister(){
            // console.log(11111111);
            // 1.用户输入用户名和密码
            var u = this.uname;
            var p = this.upwd;
            var v = this.uver;
            var eil = this.uemail;
            var t = this.uphone;
            // console.log(u+":"+p+":"+v+":"+e+":"+t);
            // 2.创建正则表达式验证用户名和密码格式是否正确
            var reg = /^[a-z0-9_]{3,8}$/i; //验证用户密码
            var regtwo = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,3}){1,2})$/;   //验证邮箱
            var regthree = /^1[34578]\d{9}$/; //验证手机号

            if(!reg.test(u)){
                //提示信息有误
                Toast("用户名格式不正确");
                return;
            }
            if(!reg.test(p)){
                Toast("密码格式不正确");
                return;
            }
            if(!(p==v)){
                Toast("两次密码不同");
                return;
            }
            if(!regtwo.test(eil)){
                Toast("邮箱格式不正确");
                return;
            }
            if(!regthree.test(t)){
                Toast("手机号码格式不正确");
                return;
            }
            // 3.发送ajax
            // console.log(3333333333);
            var url = "register?uname="+u+"&upwd="+p+"&uemail="+eil+"&uphone="+t;
            this.axios.get(url).then(result=>{
                // console.log(4444444444);                
                console.log(result);
                // console.log(result.data.code);
                if(result.data.code==1){
                    Toast("注册成功");
                    var ser = setTimeout(() => {
                        this.$router.push({ path: '/Login'});                    
                    }, 1000);
                    return;
                }
            });
            // 4.获取服务器返回结果
            // 5.提示用户
        }
    }
}
</script>
<style>
.mui-content-padded {
	margin-top: 25px;
}

.mui-btn {
	padding: 10px;
}

.link-area {
	display: block;
	margin-top: 25px;
	text-align: center;
}

.spliter {
	color: #bbb;
	padding: 0px 8px;
}
.mui-input-group .mui-input-row {
    margin-top: 10px;
}
.app-register .mui-bar-nav ~ .mui-content {
    padding-top: 0;
}
</style>



