<template>
 <div id="app" class="app-container">
    <!-- 1.公共顶部导航条 -->
   <header class="mui-bar mui-bar-nav">
		<h1 class="mui-title">融化心灵</h1>
	</header>
   <!-- 2.容器显示其他组件 -->
   <router-view v-if="isRouterAlive"></router-view>
   <!-- 3.底部tabbar -->
   <nav class="mui-bar mui-bar-tab" @click="arrHome">
		<router-link id="Home" active="#/Home" class="mui-tab-item " to="Home">
			<span class="mui-icon mui-icon-home"></span>
			<span class="mui-tab-label">首页</span>
		</router-link>
		<router-link id="shopCart" active="#/shopCart" class="mui-tab-item " to="shopCart">
			<span class="mui-icon mui-icon-extra mui-icon-extra-cart">  
           <!-- <span class="mui-badge">
               {{$store.getters.optCartCount}}
           </span> -->
         </span>
			<span class="mui-tab-label">购物车</span>
		</router-link>
		<router-link id="search" active="#/search" class="mui-tab-item" to="search">
			<span class="mui-icon mui-icon-search"></span>
			<span class="mui-tab-label">搜索</span>
		</router-link>
      <router-link id="Login" active="#/Login" class="mui-tab-item " to="personal">
			<span class="mui-icon mui-icon-contact"></span>
			<span class="mui-tab-label">用户</span>
		</router-link>
	</nav> 
 </div>
</template>

<script>
export default {
   name:"App",
   provide(){
      return{
          reload:this.reload
      }
   },
   data(){
      return {
         isRouterAlive:true,//刷新
         win:""
      }
   },
   mounted:function(){
      this.win = this.$route.path.split('/')[1];
   },
   methods:{
      arrHome(e){
         this.win = this.$route.path.split('/')[1];
         // console.log(this.win);
         // console.log(this.$route.path.split('/')[1]);
         // console.log(e);
      },
      reload(){
        this.isRouterAlive = false;
        this.$nextTick(function(){
           this.isRouterAlive = true;
        })
     }
   },
   watcher:{
      //跳转
      win:function(n){
          switch(n){
            case 'Home' :
            document.querySelector('[active="#/Home"]').classList.add(mui-active)
            break;
            case 'shopCart':
            document.querySelector('[active="#/shopCart"]').classList.add(mui-active)
            break;
            case 'search':
            document.querySelector('[active="#/search"]').classList.add(mui-active)
            break;
            case 'Login':
            document.querySelector('[active="#/Login"]').classList.add(mui-active)
            break;
          }
      }
   }
}
</script>

<style>
   .app-container{
     padding-top:40px;
     padding-bottom:50px;
     overflow-x:hidden;
   }

 .mui-bar-tab .mui-tab-item-tao.mui-active {
    color: #007aff;
 }
 .mui-bar-tab .mui-tab-item.router-link-exact-active.router-link-active{
    color: #007aff;
 }
.mui-bar-tab .mui-tab-item-tao {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item-tao .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}

.mui-bar-tab .mui-tab-item-tao .mui-icon~.mui-tab-label {
   font-size:11px;
   display:block;
   overflow:hidden;
   text-overflow:ellipsis;

}

.app-container .mui-bar.mui-bar-tab{
   width: 100%;
   margin-bottom: -5px;
   padding-bottom: 0;
   
}
</style>



