<template>
    <div class="app-newsinfo">
        <header class="mui-bar mui-bar-nav">
            <a @click="btnHome" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title"><h2>{{info.Cname}}</h2></h1>
        </header>
        <div class="mui-card">
			<div class="mui-card-header mui-card-media" :style="'height:40vw;background-image:url('+info.Csrc+')'"></div>
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
                    <div><h2>{{info.Cname}}</h2></div>
                    <div><h3>￥{{info.Cmoney}}</h3></div>
                    <hr>
                    <div><h4>{{info.Csubtitle}}</h4></div>
                    <div><h5>{{info.Cdetails}}</h5></div>
                    <hr>
                   <div class="mui-card-header mui-card-media" :style="'height:40vw;background-image:url('+info.Cimg+')'"></div>
				</div>
			</div>
            <div class="mui-card-footer">
                <mt-button type="danger" size="small" @click="addcart">
                    加入购物车
                </mt-button>
            </div>
             <div class="mui-card-footer">
                <mt-button type="danger" size="small" @click="Previouspage">
                    上一页
                </mt-button>
                <mt-button type="danger" size="small" @click="nextpage">
                    下一页
                </mt-button>
            </div>
		</div>
        <hr>
        <!-- <h1>多行文本框</h1> -->
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
                    <textarea placeholder="请吐槽最多120个字" maxlength="120" v-model="msg">
                    </textarea>
                    <mt-button size="large" type="primary" @click="addComment">发表评论</mt-button>
				</div>
			</div>
		</div>
        <!-- 新闻发表评论 end-->
        <!-- 新闻评论列表 start-->
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
                     <div class="cmt-list">
                        <div class="cmt-item" v-for="(item,i) of list" :key="i">
                            <div>第{{i+1}}楼<h4>用户:{{item.uname}}</h4> </div>
                            <div> 发表时间:{{item.ctime | datetimeFilter}}</div>
                            <div>发表内容:{{item.content}}</div>
                        </div>
                    </div>	
				</div>
			</div>
		</div>
        <!-- 新闻发表评论 end-->
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{ 
            list:[], 
            pno:0, 
            pageSize:6,
            pname:"",
            msg:"",
            nid:this.$route.query.nid,//上一个组件传递id
            info:{} //商品详细对象
        }
    },
    created(){
        // console.log(this.$route.query.nid);
        this.findNewsInfo();
        this.loadMore();
    },
    methods:{
        
        //返回上一页面
        btnHome(){
            // var ser = setTimeout(() => {
            //     this.$router.push({ path: '/Home'});
            // }, 10);
            this.$router.go(-1);
        },
        //详情下一页
        nextpage(){
            // console.log(66669999);
             // 1.获取新闻编号
             if(this.nid>=18){
                 this.nid=1;
             }else{
                 this.nid++;
             }
             
            var id = this.nid;
            // console.log(id);
            // 2.创建url保存请求地址
            var url="http://127.0.0.1:3000";
            url+="/nextpage?nid="+id;
            // 3.发送ajax请求
            this.axios.get(url).then(result=>{
                // console.log(result);
                // console.log(result.data.data[0]);
                this.info = result.data.data[0];
            //    console.log(this.info);
            })
            // 4.获取请求服务器返回数据保存 info
        },
        //详情上一页
        Previouspage(){
            // console.log(2333333);
             // 1.获取新闻编号
             if(this.nid<=1){
                 this.nid=18;
             }else{
                 this.nid--;
             }
            var id = this.nid;
            // console.log(id);
            // 2.创建url保存请求地址
            var url="http://127.0.0.1:3000";
            url+="/Previouspage?nid="+id;
            // 3.发送ajax请求
            this.axios.get(url).then(result=>{
                // console.log(result);
                // console.log(result.data.data[0]);
                this.info = result.data.data[0];
            //    console.log(this.info);
            })
            // 4.获取请求服务器返回数据保存 info
        },
        //点击添加至购物车
        addcart(){
            // console.log(11111111111);
            // 1.获取三个参数 编号 名称 价格
            // pid pname price
            var pid = this.nid;
            var pname = this.info.Cname;
            var price = this.info.Cmoney;
            var pimg = this.info.Csrc;
            // console.log(pid+":"+pname+":"+price);
            // 2.发送ajax请求
            var url="http://127.0.0.1:3000";
            url+="/addcart";
            url+="?pid="+pid;
            url+="&price="+price;
            url+="&pname="+pname;
            url+="&pimg="+pimg;
            this.axios.get(url).then(result=>{
                // console.log(result);
                //如果将商品已经加入购物车
                // this.$store.commit("increment");
                if(result.data.code==-1){
                   Toast("请登录");
                   return;
                }
            });
            // 3.获取服务器返回结果 提示23
        },
        loadMore(){
            // 1.在data添加几个数据
            // list:[] pno:0 pageSize:5
            // 2.获取pno,pageSize
            this.pno++;
            var ps = this.pageSize;
            // 3.正则表达式验证pno和pageSize是否正确
            var reg = /^[0-9]{1,}$/;
            if(!reg.test(this.pno)){
                Toast("页码格式不正确");
                return;
            }
            // console.log(this.pno+":"+ps);
            // 4.如果不正确提示
            // 5.创建url 发送ajax请求
            var url="http://127.0.0.1:3000";
            url+="/getComment?nid="+this.nid;
            url+="&pno="+this.pno;
            // 6.获取返回结果
            this.axios.get(url).then(result=>{
                // console.log(result);
                this.list=result.data.data;
                // console.log(this.list);
            })
            // 7.保存list
        },
        //获取用户名 Gettheusername
        //添加我们的评论
        addComment(e){
            // console.log(e);
            // console.log(11111);
            // console.log(this.msg);
            // 1.评论的内容要双向绑定
            // 2.评论的按钮绑定点击事件调用addComment
            // 3.获取新闻nid 获取评论内容
            var nid = this.nid;
            var con = this.msg;
            // console.log(nid+":"+con);
            // 4.去除评论两端空格
            var size = con.trim().length;
            // 5.如果评论为空提示判断
            if(size==0){
                Toast("评论内容不能为空")
                return;
            }
            // 6.创建postData保存新闻nid评论内容
            // console.log(this.qs);
            var postData = this.qs.stringify({
                nid:nid,
                content:con
            });
            // console.log(postData);
            // 7.发送post请求/addComment
            var url="http://127.0.0.1:3000";
            url+="/addComment";
            this.axios.post(url,postData).then(result=>{
                // 8.获取返回结果
                // 9.评论成功或失败 消息
                Toast("添加成功")
                //10.清空原有评论内容
                this.msg = "";
                // //11.加载评论列表
                this.pno = 0;//清空页码
                this.list = [];//清空原有数据
                this.loadMore();//第一页
            })
        },
        //查询新闻详细信息
        findNewsInfo(){
            // 1.获取新闻编号
            var id = this.nid;
            // 2.创建url保存请求地址
            var url="http://127.0.0.1:3000";
            url+="/newsInfo?nid="+id;
            // 3.发送ajax请求
            this.axios.get(url).then(result=>{
                // console.log(result);
                // console.log(result.data.data[0]);
                this.info = result.data.data[0];
            //    console.log(this.info);
            })
            // 4.获取请求服务器返回数据保存 info
        }
       
    }
}
</script>




