<template>
    <div class="app-home">
        <!-- 轮播图         mint-ui -->
        <mt-swipe :auto="8000">
            <mt-swipe-item v-for="(item,i) of navlist" :key="i">
                <a href="#">
                    <img :src="item.Wsrc" alt="">
                </a>
            </mt-swipe-item>
        </mt-swipe>
         <!-- 九宫格 mui-->
        <ul class="mui-table-view mui-grid-view mui-grid-9">
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4" v-for="item of gridlist">
                <a href="#">
                    <!-- <img :src="item.Ssrc" alt="" @click.prevent="jump" :data-id="item.id"> -->
                    <img :src="item.Ssrc" alt="" :data-id="item.Sid">
                    <div class="mui-media-body">{{item.Sname}}</div>
                </a>
            </li>
        </ul>
        <!-- 图文表格 -->
        <ul class="mui-table-view mui-grid-view table">
            <div class="am-titlebar-default" style="height:30px">
                <h2 class="am-titlebar-title" style="background-color:#efeff4; color:#f60">推荐菜品</h2>
            </div>
		    <li class="mui-table-view-cell mui-media mui-col-xs-6" v-for="(item,i) of Cbar" >
                <router-link :to="'/NewsInfo?nid='+item.Cid">
                    <a href="#">
                        <img :src="item.Csrc" :data-id="item.Cid" :data-i="i">
                        <div>{{item.Cname}}</div>
                        <div>￥{{item.Cmoney}}</div>
                    </a>
                </router-link>
            </li>
        </ul>
        <!-- 加载更多按钮 -->
        <mt-button size="large" type="danger" @click="loadMore">加载更多</mt-button>
    </div>
</template>
<script>
  export default {
    data(){
      return {
        navlist:[],//轮播图列表
        gridlist:[],//保存九宫格数组
        Cbar:[],//保存商品信息//返回的是商品列表
        pno:0 //页码
      }
    },
    created(){
        this.handleImage();
        this.handleGrid();
        this.Commoditybar();
        // this.loadMore();
    },   
    methods:{
        // jump(e){
        //     var id = e.target.dataset.id;
        //     var path = "/";
        //     if(id==1){
        //         path = "/NewsList";
        //     }else if(id==2){
        //         path = "/GoodList";
        //     }
        //     // console.log(id);
        //     this.$router.push(path);

        // },
        /*加载下一页的数据*/ 
        loadMore(){
            // 1.当前页数+1  pno:0
            this.pno++;
            // 2.创建变量url请求数据
            var url = "http://127.0.0.1:3000";
            url+="/news?pno="+this.pno;
            // console.log(url);
            // 3.发送ajax请求
            this.axios.get(url).then(result=>{
                // console.log(result);
                // console.log(result.data.data);
                //this.list = result.data.data;
                // 在模板中循环数组显示数据...
                var rows = this.Cbar.concat(result.data.data);
                this.Cbar=rows;
            });
            // 4.获取服务器数据
            // 5.将获取的数据保存到data list
        },
        Commoditybar(){
            // 1.发送ajax请求获取推荐商品数据
            var url ="http://127.0.0.1:3000";
            url+="/news";
            this.axios.get(url).then(result=>{
                 // 2.获取返回结果
                //  console.log(result.data.data);
                // 3.保存gridlist
                this.Cbar = result.data.data;
            })
        },
        handleGrid(){
            // 1.发送ajax请求获取九宫格数据
            var url ="http://127.0.0.1:3000";
            url+="/grid";
            this.axios.get(url).then(result=>{
                 // 2.获取返回结果
                //  console.log(result.data.data);
                // 3.保存gridlist
                this.gridlist = result.data.data;
            })
        },
        //加载轮播图的数据
        handleImage(){
            // 1.发送ajax请求获取轮播数据
            var url ="http://127.0.0.1:3000";
            url+="/imglist";
            this.axios.get(url).then(result=>{
                 // 2.获取返回结果
                //  console.log(result);
                //  console.log(result.data.data);
                 // 3.保存data属性中
                 this.navlist = result.data.data;
                //  console.log(this.navlist[0].Wsrc);
            })
        }
    } 
  }
</script>
<style>
/* 轮播 */
.app-home .mint-swipe{
    /* border-radius: 20px; */
    height: 200px;
}
.app-home .mint-swipe img{
    /* border-radius: 20px; */
    width: 100%;
    height: 100%;
}
/*九宫格图片宽度*/
.app-home .mui-grid-9 img{
    width: 60px;
    height: 60px; 
}
/*九宫格背景色 白色*/
.app-home .mui-grid-view.mui-grid-9{
    background-color: #fff;
}
/* 图文表格 */
h2 {
    padding-top: 5px;
    padding-bottom: 8px;
    text-indent: 12px;
    padding-left:3px;
    width: 100%;
}
.app-home .mui-table-view.mui-grid-view{
    padding: 0 ;
}
.app-home .mui-table-view-cell.mui-media.mui-col-xs-6>a:not(.mui-btn) {
    margin: 17px 10px 7px -15px;
}
/*图片名称*/
.app-home .mui-table-view.mui-grid-view.table .mui-col-xs-6 a div:nth-child(2){
    border: 1px solid #000;
    width: 100%;
    margin-top: 10px;
    font-weight: 400;
    font-size: 14px;
    display: block;
    word-wrap: normal;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #555;
      
}
/* 图片价格 */
.app-home .mui-table-view.mui-grid-view.table .mui-col-xs-6 a div:nth-child(3){
    float: left;
    font-style: normal;
    font-size: 15px;
    color: #f60;
}

/*图片宽度*/
.app-home .mui-table-view.mui-grid-view.table .mui-col-xs-6 img{
    width: 100%;
}

/*背景色 白色*/
.app-home .mui-table-view.mui-grid-view.table{
    background-color: #fff;
}

</style>


