<template>
    <div class="app-personal">
        <header class="hearder-top mui-bar mui-bar-nav">
            <a @click="apppersonal" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title">个人中心</h1>
        </header>
        <div class="mui-card-content">
            <div class="mui-card-content-inner ">
                <!-- 登录 -->
                <div v-if="zz==false" class='mui-card-ul' >
                    <div class="mui-card-header app-personal-top">
                        <div class="mui-card-content"></div>
                        <div class="mui-card-content">
                            <div class="mui-card-content-inner"> 
                                <div>
                                    <mt-button size="large" type="primary" @click="goLogin">请登录</mt-button>
                                </div>
                            </div>
                        </div>
                        <div class="mui-card-content"></div>
                    </div> 
                </div>
                <div v-if="zz==true" class='mui-card-list'>
                    <div class="mui-card-header app-personal-top">
                        <div class="mui-card-content">
                            <img class="mui-card-content-img" :src="this.img" alt="">
                        </div>
                        <div class="mui-card-content">
                            <div class="mui-card-content-inner"> 
                                <div>
                                    <h4>用户名：{{tmp}}</h4>
                                </div>
                            </div>
                        </div>
                        <div class="mui-card-content"></div>
                    </div> 
                </div>
            </div>
        </div>
                <!-- 注销 -->
       <div v-if="zz==true" class="mui-card">
			<div class="mui-card-content">
			    <div class="mui-card-content-inner">
                    <mt-button size="large" type="danger" @click="Logoutlogin">注销登录</mt-button>
                </div>
			</div>
		</div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
  data(){
    return{
        zz:false,
        isActive: true,
        isSort: false,
        tmp:"",
        img:""
    }
  },
  created(){
        // console.log(this.Uname);
        this.loadMore();
        this.Userhead();
    },
    methods:{
        //获取头像
        Userhead(){
            var url = "Userhead";
            this.axios.get(url).then(result=>{
                // console.log(result);
                if(true){
                    // console.log(result.data.data[0].Uimg);
                    this.img=result.data.data[0].Uimg ||"img/Userhead/01.jpg";
                }
            });
        },
        //获取用户名
        loadMore(){
            var url = "Uacquisition";
            this.axios.get(url).then(result=>{
                // console.log(4444444444);                
                // console.log(result);
                // console.log(result.data.code);
                // console.log(result.data.data[0]);
                if(result.data.code==1){
                    this.tmp = result.data.data[0].Uname
                    this.Userhead();
                    this.zz=true;
                    return;
                }else if(result.data.code==-1){
                    this.zz=false;
                }
            });
        },
        //注销登录
        Logoutlogin(){
          var url = "Cancellationlanding";
            this.axios.get(url).then(result=>{
                // console.log(4444444444);                
                // console.log(result);
                // console.log(result.data.code);
                // console.log(result.data.data[0]);
                if(result.data.code==1){
                    this.tmp = result.data.data[0].Uname
                    this.zz=true;
                    return;
                }else if(result.data.code==-1){
                    this.zz=false;
                }
            });
        },
        //跳转到登录页面
        goLogin(){
            this.$router.push({ path: '/Login'});
        },
        //返回上一个操作页面
        apppersonal(){
            this.$router.go(-1);
        },
        onhide(){
            this.isActive=false,
            this.isSort=true
        },
        onshow(){
            this.isActive=true,
            this.isSort=false
        }
    }
}
</script>
<style>
.app-personal{
    margin: 0;
    padding: 0;
}
.app-personal .mui-card.app-personal-top{
    width: 100%;
    position: relative;
    top: 0%;
    left: -3%;
}
.show{display:block !important;}
.hide{display:none !important;}

.set-head {
    position: relative;
    top: .7em;
  }
.set-head-icon {
    width: 2em;
    border-radius: 50%;
  }
.dn {
    display: none;
  }
/* 头像大小 */
.mui-card-content .mui-card-content-img{
    height:80px;
    width:80px;
    border:1px solid #000;
    border-radius:50%
}
</style>





