import Vue from 'vue'
import App from './App.vue'
import router from './router'
//脚手架main.js
Vue.config.productionTip = false
// 0.1引入Vuex组件
import Vuex from "vuex"
// 0.2注册Vuex组件
Vue.use(Vuex)
// 0.3创建store对象
var store = new Vuex.Store({
  //共享数据容器
  state:{
    cartCount:sessionStorage.getItem("cartCount")||0,
  },
  //操作共享数据方法
  mutations:{
    increment(state){
      state.cartCount++;
      sessionStorage.setItem("cartCount",state.cartCount);
    }
  },
  //获取共享数据方法
  getters:{
    optCartCount:function(state){
      return state.cartCount;
    }
  }
})
// 0.4将store绑定Vue实例(底部)





import'mint-ui/lib/style.css'
import './lib/mui/css/mui.css'
import './lib/mui/css/mui.min.css'
// 导入 MUI 的样式表， 和 Bootstrap 用法没有差别
// 导入 MUI 的样式表，扩展图标样式，购物车图标

// 还需要加载图标字体文件
import './lib/mui/css/icons-extra.css'

//导入mui组件

// import "./lib/mui/js/mui.js"
// import "./lib/mui/js/mui.min.js"



// 1.引入Header,Button组件
import {
  Header,
  Button,
  Swipe, //引入轮播图组件
  SwipeItem//引入轮播图组件
} from "mint-ui"
// 2.注册--组件Header,Button组件
Vue.component(Header.name,Header);
Vue.component(Button.name,Button);
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);

//3.引入axios模块
import axios from "axios"
//4.配置跨域访问
axios.defaults.withCredentials=true

//5.将axios配置到vue实例属性
Vue.prototype.axios=axios

//创建一个全局过滤器，使其能够被任意组件调用
//6.创建一个日期格式过滤器 
Vue.filter("datetimeFilter",function(val){
    // 1.依据原始日期格式创建日期对象
    var now = new Date(val); 
    // 2.获取日期对象 年月日
    var y = now.getFullYear();
    var m = now.getMonth()+1;
    var d = now.getDate();
    // 3.获取日期对象 时分秒
    var h = now.getHours();
    var mi = now.getMinutes();
    var s = now.getSeconds();
    // 4.月份与日期 如果当前月份小于10前拼接0
    m<10&&(m=="0"+m);
    d<10&&(d=="0"+d);
    // 5.拼接字符串返回
    return `${y}-${m}-${d} ${h}:${mi}:${s}`;
})

//引入qs模块,解决axios post请求问题
import qs from "qs";
Vue.prototype.qs = qs;

//弹出框
import {
  Popup
} from 'mint-ui';
Vue.component(Popup.name, Popup);


new Vue({
  router,
  render: h => h(App),
  store      // 0.4将store绑定Vue实例
}).$mount('#app')



